  animation: cloudy 5s ease-in-out infinite;
}
.rainy:after {
  content: "";
  position: absolute;
  top: 250px;
  left: 50%;
  height: 15px;
  width: 120px;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  animation: cloudy_shadow 5s ease-in-out infinite;
}
@keyframes cloudy {
